body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #170257;
}
*{
  font-family: 'Roboto', sans-serif;
}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
ul{
  list-style: none;
  padding: 0;
}
#root{
  display: flex;
  justify-content: center;
  align-items: center;
}
ul li:first-child{
  font-size: x-large;
  font-weight: bold;
}
li:nth-child(2){
  font-size: larger;
}
li:nth-child(4){
  font-size: medium;
  text-transform: capitalize;
}
.contenido{
  background-color: white;
  margin: 1rem;
  text-align: center;
  align-items: center;
  width: 25%;
  border: 1px solid #170257;
  border-radius: 10px;
}
.formulario{
  margin-top: 20px;
  border-bottom: 1px solid gray;
  padding: 10px 20px;
}
.datos_ciudad{
  padding: 10px 20px;
}
input[type=text], select {
  width: 100%;
  padding: 12px 20px;
  margin: 8px 0;
  display: inline-block;
  border: 1px solid #ccc;
  border-radius: 4px;
  box-sizing: border-box;
}
  button{
  width: 100%;
  background-color: #4CAF50;
  color: white;
  margin-top: 10px;
  padding: 14px 20px;
  margin: 8px 0;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  font-weight: bold;
}
button:hover{
  background-color: #429945;
  transition: 0.3s;
}
ul li{
  margin-top: 10px;
}